import React from "react";
import style from "./main.module.css"
import { FaRegFileAlt, FaFileDownload } from "react-icons/fa";
const Main = (props) => {

    let mainArray = [];
    let resultArray = [];
    let countItems = 0;
    let result = '';
    let files = [];

    const onFiles = (e) => {

        mainArray = [];
        resultArray = [];
        countItems = 0;
        result = '';

        files = e.target.files;

        for (let i = 0; i < files.length; i++) {
            let reader = new FileReader();
            reader.readAsText(files[i]);

            reader.onload = function () {
                let res = reader.result.split('\r');
                let tempArray = new Array();
                for (let j = 0; j < res.length; j++)  {
                    if (res[j].trim() !== '') {
                        tempArray.push(res[j].replace(/(\r\n|\n|\r)/gm,""));
                        countItems += 1;
                    }
                    console.log(reader.load);
                }
                mainArray.push(tempArray)
            };

            reader.onerror = function () {
                console.log(reader.result);
            };

        }

    }



    const onResult = (props) => {

        console.log(mainArray)
        console.log(countItems)

        for (let p = 0; p < countItems; p++) {

            for (let h = 0; h < mainArray.length; h++) {

                if (mainArray[h][p] !== undefined) {
                    resultArray.push(mainArray[h][p])
                }

            }
        }

        console.log(resultArray);

        result = resultArray.join('\n');
        resultArray = [];
        function download(filename, result) {
            let element = document.createElement('a');
            element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(result));
            element.setAttribute('download', filename);
            element.style.display = 'none';
            document.body.appendChild(element);
            element.click();
            document.body.removeChild(element);
        }

        download('hello.txt', result);

    }

    return (
        <div className="container-lg center">

            <div className={style.inputWrapper}>
                <input onChange={onFiles} name="file" type="file" name="file" id="inputFile" className={style.inputFileStyle} multiple />
                    <label htmlFor="inputFile" className={style.inputFileButton}>

                        <FaRegFileAlt className={style.inputFileIconWrapper} />
                        <span className={style.inputFileButtonText}>Выберите файлы</span>
                    </label>
            </div>

                <br />
                <button onClick={onResult} type="submit" className={style.inputFileButton}>
                    <FaFileDownload className={style.inputFileIconWrapper} />
                    <span>Скачать результат</span>
                </button>

        </div>
    )
}

export default Main;